import {sleep} from './sleep.js';

/* MediaDevices manipulation */

export default class StreamRecorder {

  constructor(inputStream){

    this._inputStream = inputStream;
    this._recordingStream = undefined;

    this._chunks = [];
    this._exportBlob = null;

    this._recording = false;
    this._initialized = false;
    this._recorder = null;
    this._init = this._init.bind(this);

    this.start = this.start.bind(this);
    this.stop = this.stop.bind(this);
    this.getBlob = this.getBlob.bind(this);
    this.exportFile = this.exportFile.bind(this);
    this.clearStream = this.clearStream.bind(this);

    this._init();

  }

  _init() {

    let vtrack = undefined;
    let atrack = undefined;
    this._recordingStream = new MediaStream();

    // record one video and audio track at most
    this._inputStream.getTracks().forEach(track => {
      if (!track.enabled) return;
      if (!atrack && track.kind === 'audio') {
        this._recordingStream.addTrack(track);
        atrack = 1;
      }
      if (!vtrack && track.kind === 'video') {
        this._recordingStream.addTrack(track);
        vtrack = 1;
      }
    });

    let mimeType = '';

    if (vtrack && atrack) mimeType = 'video/webm;codecs=vp8,opus';
    else if (vtrack && !atrack) mimeType = 'video/webm;codecs=vp8';
    else if (!vtrack && atrack) mimeType = 'audio/opus;codecs=opus';
    else {
      console.error('No tracks to record');
      return;
    }

    const options = {
      mimeType: mimeType,
      audioBitsPersecond: 510000
    };

    try {
      this._recorder = new MediaRecorder(this._recordingStream, options);
      this._initialized = true;
    } catch (e) { // for Safari
      let noMimeTypeOptions = options;
      delete options.mimeType;
      this._recorder = new MediaRecorder(this._recordingStream, noMimeTypeOptions);
      this._initialized = true;
    }

    this._recorder.onstop = async (e) => { 
      console.log('onstop');
      this._exportBlob 
        = await new Blob(this._chunks, {type: this._chunks[0].type});
    }

    this._recorder.ondataavailable = (e) =>  {
      console.log('ondataavailable');
      this._chunks.push(e.data); 
    };

  }

  start() {
    if (!this._initialized) return false;
    if (this._recording) return false;

    console.log('recording start', this._inputStream.getTracks());
    this._recorder.start(); 
    this._recording = true;
    return true;
  }

  async stop() { 
    if (!this._recording) return null;

    console.log('stop');
    await this._recorder.stop(); 
    console.log('stop complete');

    while (this._exportBlob === null) await sleep(1000);

    this._recording = false;
    return this._exportBlob;
  } 

  pause() {
    if (this._recorder.state === 'inactive') return false;
    this._recorder.pause();
    return true;
  }

  resume() {
    if (this._recorder.state !== 'inactive') return false;
    this._recorder.resume();
  }

  async getBlob() { 
     if (this.recording) await this.stop();
     return this._exportBlob; 
  }

  clearStream() {
    if (this._recording) return false;

    this._recordinstream.getTracks().forEach (track => {
          this._recordingStream.removeTrack(track);}); 

    return true; 
  }

  async exportFile(name){

    const type = this._exportBlob.type; 
    const tmp = type.split(';')[0];
    const extension = tmp.split('/')[1];

    const filename = `${Date.now()}_${name}.` + extension;
    // console.log(filename);

    try {
      const saveLink = document.createElement("a");
      saveLink.href = URL.createObjectURL(this._exportBlob);
      saveLink.download = filename;
      document.body.appendChild(saveLink);
      saveLink.click();
      document.body.removeChild(saveLink);
      return true;
    } catch(err) {
      console.error(err);
      return false;
    }
    
  }

};
