export default class InputAudioEffects {

  constructor(ctx,inputStream,meterCallback){
    this._ctx = new (window.AudioContext || window.webkitAudioContext) ();
    // this._ctx = ctx;
    console.log('AudioContext', this._ctx);
    this._inputStream = inputStream;
    this._meterCallback = meterCallback;
    this._source = undefined;
    this._destination = undefined;
 
    this._gainNode = undefined; 
    this._biquadFilterNode = undefined;
    this._loop = this._loop.bind(this);

    this.getOutputStream = this.getOutputStream.bind(this);
    this.getOutputTrack = this.getOutputTrack.bind(this);
    this.getInputTrack = this.getInputTrack.bind(this);
    this.setGain = this.setGain.bind(this);
    this.close = this.close.bind(this);
    this.setAutoGain = this.setAutoGain.bind(this);
    this.setEchoCancellation = this.setEchoCancellation.bind(this);
    this.setComp = this.setComp.bind(this);

    this.applyEffects();
  }

  setAutoGain(boolean){
    const track = this.getInputTrack();
    const constraints = track.getConstraints();
    constraints.autoGainContrl = boolean;
    track.applyConstraints(constraints);
  }

  setEchoCancellation(boolean){
    const track = this.getInputTrack();
    const constraints = track.getConstraints();
    constraints.echoCancellation = boolean;
    track.applyConstraints(constraints);
  }

  setComp(boolean){
  }

  getOutputStream() {
    if(this._destination){ 
      // console.log(this.constructor.name, 'Out track-id:',  
      // this._destination.stream.getAudioTracks()[0].id);
      return this._destination.stream;
    } else return null;
  }

  getOutputTrack() {
    return this.getOutputStream().getTracks()[0];
  }

  getInputTrack() {
    return this._inputStream.getTracks()[0];
  }

  setGain(gain){
    this._gainNode.gain.value = Math.pow(10,gain/20); // dB
  }

  close(){ 
    console.log(this.constructor.name, 'close()');
    this._inputStream.getTracks()[0].stop(); // audio track only
    this._ctx.close(); 
  }

  applyEffects(){
    this._ctx.resume();

    this._source = this._ctx.createMediaStreamSource(this._inputStream);
    this._destination = this._ctx.createMediaStreamDestination();
    this._biquadFilterNode = new BiquadFilterNode(this._ctx, {type: 'allpass'});
    this._gainNode = new GainNode(this._ctx);
    this._gainNode.gain.value = 1.0; // 0dB
    this._analyserNode = new AnalyserNode(this._ctx, {fftSize: 2048});

    this._source.connect(this._biquadFilterNode);
    this._biquadFilterNode.connect(this._gainNode);
    this._gainNode.connect(this._analyserNode);
    this._gainNode.connect(this._destination);

//  this._gainNode.connect(this._ctx._destination); // local monitor

    this._loop();
  }


/* Reference
 https://stackoverflow.com/questions/44360301/web-audio-api-creating-a-peak-me ter-with-analysernode 
*/
  _loop(){
    const sampleBuffer = new Float32Array(this._analyserNode.fftSize);

    this._analyserNode.getFloatTimeDomainData(sampleBuffer);
    let peakPower = 0; 
    for (let i=0; i < sampleBuffer.length; i++)
        peakPower = Math.max(sampleBuffer[i]**2, peakPower);

    const peakPowerDB = Math.max(-100, 10*Math.log10(peakPower));

    this._meterCallback(peakPowerDB); // peakPowerDB

    requestAnimationFrame(this._loop);
  } //end loop()

};
